  /*
   * Стили компонента App
   */

    * {
      box-sizing: border-box;
    }
    ul {
      list-style: none;
      margin: 0;
      padding: 0;
    }
    
    h1,
    h2 {
      text-align: center;
    }
    .App {
      display: grid;
      grid-template-columns: 1fr;
      grid-gap: 16px;
      padding-bottom: 24px;
  
      width: 100%;
      text-align: center;
    }
  

  
  
 
  
 